  .menu-side-w ul.sub-menu {
    padding-left: 37px; }
  .menu-side-w {
    width: 260px; }
    .menu-side-w .logged-user-menu ul {
      padding-left: 30px;
      padding-right: 30px; }
  .support-index .support-ticket-content-w .ticket-reply .ticket-reply-info .badge {
    position: absolute;
